import React from 'react'

const NavBarFooter = (props) => {

    const style = {margin: "5px"};

    return (
        <table id="footer">
            <tbody>
                <tr>
                    <td><p className="navigation-link">
                        <a href="rules.php"></a>
                        <span style={style}>|</span>
                        <a href="about.php"></a>
                        <span style={style}>|</span>
                        <b><a href="ad.php">  </a>  </b>
                        <span style={style}>|</span>
                        <a href="find.php"></a>
                        <span style={style}>|</span>
                        <noindex>
                            <a rel="nofollow" href="sections_list.php" ></a>
                            <span style={style}>|</span>
                            <a rel="nofollow" href="rating.php"></a>
                            <span style={style}>|</span>
                            <a href="http://kb.mista.ru"> </a>
                            <span style={style}>|</span>
                            <a href="http://wiki.mista.ru">- (2)</a>
                            <span style={style}>|</span>
                            <a rel="nofollow" href="http://m.mista.ru/"></a>
                            <span style={style}>|</span>
                            <a href="archive.php"></a>
                            <span style={style}>|</span>
                            <a href="moders.php"></a>
                            <span style={style}>|</span>
                            <a href="users_gallery.php"></a>
                            <span style={style}>|</span>
                            <span>18+</span>
                        </noindex>
                    </p>
                    </td>
                </tr>
            </tbody>
        </table>
    )
}

export default NavBarFooter;


